@import "../../common/styles/sass/variables.scss";

.myProgectBlock {
    width: 330px;
    /* width: calc(100%/3); */
    display: flex;
    flex-direction: column;
    align-items: center;
    @media screen and (max-width: 767px){           
         width: 100% ;
         margin: 0 0 30px;     
    }
    .imageContainer {
       width: 100%;
       height: 300px;
       background-color: rgb(207, 185, 185);    
       background-size: cover;
       background-position: center;
       display: flex;
       justify-content: center;
       align-items: center;
       position: relative;
       &::before {
        position: absolute;
        content: "";
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        background-color: $primaryColor;
        opacity: 0.4;
    }
    }
}




